"use client";

import { Home, Building } from "lucide-react";

interface PriceSectionProps {
  type: "residential" | "commercial";
}

export default function PriceSection({ type }: PriceSectionProps) {
  const isResidential = type === "residential";

  const sectionTitle = isResidential ? "Residential Price" : "Commercial Price";
  const icon = isResidential ? (
    <Home className="w-5 h-5 text-[var(--color-text-primary)]" />
  ) : (
    <Building className="w-5 h-5 text-[var(--color-text-primary)]" />
  );
  const prefix = isResidential ? "res" : "com";

  const placeholderRates = isResidential
    ? { hourly: "$50/hr", day: "$300/day", emergency: "$75/hr" }
    : { hourly: "$60/hr", day: "$350/day", emergency: "$90/hr" };

  return (
    <div>
      {/* Section Header */}
      <div className="flex items-center gap-2 mb-3">
        {icon}
        <span className="font-semibold text-[var(--color-text-primary)]">
          {sectionTitle}
        </span>
      </div>

      {/* Price Card */}
      <div className="border border-[var(--color-btn-teritary-200)] rounded-xl p-4 space-y-4 bg-[var(--color-bg-neutral-50)] shadow-sm">
        {/* Hourly + Day Rate */}
        <div className="grid grid-cols-2 gap-4">
          <div>
            <label className="text-sm text-[var(--color-text-primary)]">
              Standard Hourly Rate
            </label>
            <input
              type="text"
              placeholder={placeholderRates.hourly}
              className="w-full text-[var(--color-text-primary)] border border-[var(--color-btn-teritary-300)] rounded-lg px-3 py-2 mt-1 text-sm 
                focus:ring-2 focus:ring-[var(--color-active-line)] focus:outline-none"
            />
            <p className="text-xs text-[var(--color-text-muted)] mt-1">
              Min: $25/hr - Max: $500/hr
            </p>
          </div>

          <div>
            <label className="text-sm text-[var(--color-text-primary)]">
              Standard Day Rate
            </label>
            <input
              type="text"
              placeholder={placeholderRates.day}
              className="w-full text-[var(--color-text-primary)] border border-[var(--color-btn-teritary-300)] rounded-lg px-3 py-2 mt-1 text-sm 
                focus:ring-2 focus:ring-[var(--color-active-line)] focus:outline-none"
            />
            <p className="text-xs text-[var(--color-text-muted)] mt-1">
              Typically 1.5√ó standard rate
            </p>
          </div>
        </div>

        {/* Emergency Rate */}
        <div>
          <label className="text-sm text-[var(--color-text-primary)]">
            Emergency/After-Hours Rate
          </label>
          <input
            type="text"
            placeholder={placeholderRates.emergency}
            className="w-full text-[var(--color-text-primary)] border border-[var(--color-btn-teritary-300)] rounded-lg px-3 py-2 mt-1 text-sm 
              focus:ring-2 focus:ring-[var(--color-active-line)] focus:outline-none"
          />
          <p className="text-xs text-[var(--color-text-muted)] mt-1">
            Typically 1.5√ó standard rate
          </p>
        </div>

        {/* Quote Checkbox */}
        <div className="flex items-center mt-2">
          <input
            type="checkbox"
            id={`${prefix}-quote`}
            className="w-4 h-4 text-[var(--color-primary)] border-[var(--color-btn-teritary-300)] rounded focus:ring-[var(--color-active-line)]"
          />
          <label
            htmlFor={`${prefix}-quote`}
            className="ml-2 text-sm text-[var(--color-text-secondary)] cursor-pointer"
          >
            Request for Quote
          </label>
        </div>
      </div>
    </div>
  );
}
